<template>
  <div id="app">
    <router-view />
    <el-radio-group v-model="server_type" class="pf z_index999 server_type" >
      <el-radio-button label="java"></el-radio-button>
      <el-radio-button label="python"></el-radio-button>
    </el-radio-group>
  </div>
</template>

<script>
import Cookies from "js-cookie";
export default {
  name: "App",
  data(){
    return{
      server_type:"java"
    }
  },
  watch:{
    "server_type"(){
      localStorage.setItem("server_type", this.server_type);
    }
  },
  created() {
    let server_type= localStorage.getItem("server_type")
    if(server_type){
      this.server_type=server_type
    }
    //处理登录逻辑
    let userInfo = JSON.parse(localStorage.getItem("userInfo"));
    if (userInfo && userInfo.user_id && userInfo.refresh_jwt) {
      this.$post("check/refreshJWT")
        .then(res => {
          if (!res.user_id) {
            this.$notify({
              title: "提示",
              message: res,
              type: "warning"
            });
            localStorage.removeItem("userInfo");
            this.$router.replace("/");
            return;
          }
          localStorage.setItem("userInfo", JSON.stringify(res));
          if (this.$route.path == "/")
            this.$router.replace("/page/user/userList");
        })
        .catch(res => {
          localStorage.removeItem("userInfo");
          this.$router.replace("/");
          loading.close();
        });
    } else {
      this.$router.replace("/");
    }
  }
};
</script>

<style>
.server_type{
  bottom: 0;
}
</style>



